/* ===================================
    
   =================================== */

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
}

#root {
  height: 100vh;
  overflow: hidden;
}

/* ===================================
     
   =================================== */

/*    */
.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  
  /*  fixed     */
  padding-top: 60px;         /*   */
  
  /*    -  90%  */
  width: 95%;
  min-width: 1200px;
  margin: 0 auto;            /*    */
}

/*   */
.header {
  flex-shrink: 0;
  z-index: 1000;
}

/*    */
.content {
  flex: 1;                   /*     */
  display: flex;
  flex-direction: column;
  padding: 20px 0;           /*   */
  overflow: hidden;          /*   */
  min-height: 0;             /* Flexbox    */
}

/*    */
.tab-manager {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;             /* Flexbox    */
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;          /*     */
}

/*   () */
.footer {
  flex-shrink: 0;
  padding-bottom: 20px;
}

/* ===================================
    
   =================================== */

/*   */
@media (max-width: 1400px) {
  .app {
    width: 95%;
    min-width: 1000px;
  }
}

/*   */
@media (max-width: 1200px) {
  .app {
    width: 98%;
    min-width: 900px;
  }
  
  .content {
    padding: 15px 0;
  }
}

/*  ( ) */
@media (max-width: 900px) {
  .app {
    width: 100%;
    min-width: 100%;
    margin: 0;
    padding-top: 60px;       /*     */
  }
  
  .content {
    padding: 10px 0;
  }
}