body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Tabulator    */
.tabulator {
  font-size: 1.3rem !important;  /* 13px */
}

/*   */
.tabulator .tabulator-header .tabulator-col {
  font-size: 0.75rem !important;
  background-color: #f5f5f5 !important;
}

.tabulator .tabulator-header .tabulator-col {
  padding: 0px 0px !important;  /*    */
  height: 50px !important;       /*    */
}

/*   */
.tabulator .tabulator-cell {
  padding: 4px 6px !important;   /*    */
  font-size: 0.8rem !important;
  line-height: 1;              /*   */
  box-sizing: border-box;  /* border    */
}

/*   */
.tabulator .tabulator-row {
  min-height: 22px !important;   /*    */
}

.tabulator-row .tabulator-cell {
  height: 27px !important;
}

/* border  box-shadow  */
.tabulator-cell.tabulator-editable {
  box-shadow: inset 0 0 0 1.5px #878d92;
  background-color: #ffffff;
  position: relative;
}

/*   */
.tabulator-cell.tabulator-editable:hover {
  border: 1.5px solid #0d6efd;
  background-color: #e7f1ff;
  cursor: text;
}

.tabulator-cell.tabulator-editable:hover::after {
  opacity: 1;
  color: #0d6efd;
}

