/* ============================================
   Tabulator    
   ============================================ */

/*    */
.tabulator .tabulator-header .tabulator-col {
  border-right: 1px solid #6c757d !important;
}

/*     */
.tabulator .tabulator-tableholder .tabulator-table .tabulator-row .tabulator-cell {
  border-right: 1px solid #6c757d;
}

/* hover    */
.tabulator .tabulator-row:hover .tabulator-cell {
  border-right: 1px solid #6c757d;
}

/*     */
.tabulator .tabulator-row.tabulator-selected .tabulator-cell {
  border-right: 1px solid #6c757d;
}

/*      */
.tabulator .tabulator-row .tabulator-cell.tabulator-editing {
  border-right: 1px solid #6c757d;
}

/* ============================================
   Tabulator    
   ============================================ */

/*   box-sizing  */
.tabulator .tabulator-cell {
  box-sizing: border-box !important;
}

/*     */
.tabulator .tabulator-row .tabulator-cell.merged-group-top {
  border-top: 3px solid #495057 !important;
  margin: 0 !important;
}

.tabulator .tabulator-row .tabulator-cell.merged-group-bottom {
  border-bottom: 3px solid #495057 !important;
  margin: 0 !important;
}

.tabulator .tabulator-row .tabulator-cell.merged-cell-hidden {
  border-top: none !important;
  color: transparent !important;
  user-select: none !important;
  background-color: #ffffff !important;
}

